*, *:before, *:after {
    outline: none;
    box-sizing: inherit;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body{
    font-family: $mainfont;
    font-weight: 400;
    font-size: 16px;
    
    color: $mainfontcolor;
    line-height: 1.625;
}

img {
    max-width: 100%;
    width: 100%;
}