.navbar {
    background-color: $secColor;
    padding: 20px;

    .header{    
        .navbar{
            &-brand {
                width: 23%;
                margin-right: 50px;
            }
    
            &-nav .nav-link {
                color: var(--light);
                padding-left: 15px;
                padding-right: 15px;
            }
        }
    }
}

.jumbotron {
    background-color: $maincolor;
    color: var(--light);
}